<template>
	<view>
		<view class="page">
			<scroll-view scroll-y class="scrollView">
				<view class="utilizationView">
					<ch-circle-progress :percent="80" :activeColor="'#34ab15'" bgColor="transparent" borderWidth="20"
						width="250">
						<view class="percent">80 <text>%</text></view>
					</ch-circle-progress>
					<view class="utilizationView_rate">
						<view class="utilizationView_rate_day">
							<text>昨日设备利用率</text>
							<view class="progressView_pro"><u-line-progress :percentage="30" :showText="false"
									height="20"></u-line-progress></view>
						</view>
						<view class="utilizationView_rate_day">
							<text>近7日设备利用率</text>
							<view class="progressView_pro"><u-line-progress :percentage="30" :showText="false"
									height="20"></u-line-progress></view>
						</view>
					</view>
				</view>
				<u-collapse :value="[0, 1]">
					<u-collapse-item class="collapseItem" v-for="(item, index) in cardList" :key="index" :title="item.title"
						:name="index" :isLink="false">
						<view style="height: 20px;width: 2px;background-color: #FF6600;margin-right: 5px;" slot="icon">
						</view>
						<view v-if="index === 0">
							<view class="progressView">
								<view class="progressView_num">
									<view class="progressView_num_content">
										<text>当前完成</text>
										<text>7500</text>
									</view>
									<view class="progressView_num_line"></view>
									<view class="progressView_num_content">
										<text>今日完成</text>
										<text>23000</text>
									</view>
								</view>
								<view class="progressView_pro">
									<u-line-progress :percentage="30" :showText="false" height="20"></u-line-progress>
								</view>
							</view>
						</view>
						<view v-if="index === 1">
							<u-grid :border="false" col="4">
								<u-grid-item v-for="(listItem, listIndex) in list" :key="listIndex" @click="itemClick">
									<u-icon :customStyle="{ paddingTop: 20 + 'px' }" :name="listItem.name"
										:size="22"></u-icon>
									<text class="grid-text">{{ listItem.title }}</text>
								</u-grid-item>
							</u-grid>
							<u-toast ref="uToast" />
						</view>
					</u-collapse-item>
				</u-collapse>
			</scroll-view>
		</view>
	</view>
</template>

<script>

export default {
	data() {
		return {
			cardList: [
				{ title: "今天计划进度", id: 1 },
				{ title: "报表", id: 2 },
			],
			list: [{
				name: 'photo',
				title: '图片'
			},
			{
				name: 'lock',
				title: '锁头'
			},
			{
				name: 'star',
				title: '星星'
			},
			{
				name: 'hourglass',
				title: '沙漏'
			},
			{
				name: 'home',
				title: '首页'
			},
			{
				name: 'star',
				title: '音量'
			},
			],
		};
	},
	onShow() {
		uni.setNavigationBarTitle({ // 修改头部标题
			title: this.$t("report.name")
		});
	},
}
</script>

<style lang="scss" scoped>
.scrollView {
	height: calc(100vh - calc(var(--status-bar-height) + 0.64rem));
}

.collapseItem {
	background-color: white;
}

.utilizationView {
	width: 100%;
	height: 300upx;
	display: flex;
	flex-direction: row;
	margin-bottom: 10px;
	background-color: white;

	&_circle {
		width: 50%;
		height: 100%;
		display: flex;
		align-items: center;
		justify-content: center;
	}

	&_rate {
		width: 50%;
		height: 100%;
		display: flex;
		flex-direction: column;

		&_day {
			width: 100%;
			height: 50%;
			display: flex;
			flex-direction: column;
			justify-content: center;
		}
	}
}

.progressView {
	width: 100%;
	height: 200upx;
	display: flex;
	flex-direction: column;

	&_num {
		width: 100%;
		height: 50%;
		display: flex;
		flex-direction: row;
		align-items: center;

		&_content {
			width: 100%;
			height: 100%;
			display: flex;
			flex-direction: column;
			color: white;
			text-align: center;
			justify-content: center;

			text {
				padding: 2px;
				color: #909399;
			}
		}

		&_line {
			width: 1%;
			height: 30%;
			background-color: #909399;
		}
	}

	&_pro {
		width: 100%;
		height: 50%;
		display: flex;
		align-items: center;
	}
}

.u-progress-content {
	display: flex;
	align-items: center;
	justify-content: center;
}

.u-progress-dot {
	width: 16rpx;
	height: 16rpx;
	border-radius: 50%;
	background-color: #fb9126;
}

.u-progress-info {
	font-size: 28rpx;
	padding-left: 16rpx;
	letter-spacing: 2rpx;
	color: #fb9126;
}

.grid-text {
	font-size: 14px;
	color: #909399;
	padding: 10rpx 0 20rpx 0rpx;
	/* #ifndef APP-PLUS */
	box-sizing: border-box;
	/* #endif */
}
</style>
